export default "import { NgModule } from \"@angular/core\";\nimport { FormsModule, ReactiveFormsModule } from \"@angular/forms\";\nimport { RouterModule } from \"@angular/router\";\nimport {\n    DEMO_PATH_TOKEN,\n    NuiButtonModule,\n    NuiCheckboxModule,\n    NuiDocsModule,\n    NuiMessageModule,\n    SrlcStage,\n} from \"@nova-ui/bits\";\n\nimport {\n    CheckboxBasicExampleComponent,\n    CheckboxDisabledExampleComponent,\n    CheckboxExampleComponent,\n    CheckboxHelpHintExampleComponent,\n    CheckboxIndeterminateExampleComponent,\n    CheckboxInFormExampleComponent,\n    CheckboxLinkExampleComponent,\n    CheckboxOutputExampleComponent,\n    CheckboxTestComponent,\n    CheckboxVisualTestComponent,\n} from \"./index\";\n\nconst routes = [\n    {\n        path: \"\",\n        component: CheckboxExampleComponent,\n        data: {\n            \"srlc\": {\n                \"stage\": SrlcStage.ga,\n            },\n            showThemeSwitcher: true,\n        },\n    },\n    {\n        path: \"checkbox-visual-test\",\n        component: CheckboxVisualTestComponent,\n        data: {\n            \"srlc\": {\n                \"hideIndicator\": true,\n            },\n        },\n    },\n    {\n        path: \"checkbox-test\",\n        component: CheckboxTestComponent,\n        data: {\n            \"srlc\": {\n                \"hideIndicator\": true,\n            },\n        },\n    },\n];\n\n@NgModule({\n    imports: [\n        NuiCheckboxModule,\n        FormsModule,\n        ReactiveFormsModule,\n        NuiMessageModule,\n        NuiButtonModule,\n        NuiDocsModule,\n        RouterModule.forChild(routes),\n    ],\n    declarations: [\n        CheckboxDisabledExampleComponent,\n        CheckboxHelpHintExampleComponent,\n        CheckboxIndeterminateExampleComponent,\n        CheckboxInFormExampleComponent,\n        CheckboxOutputExampleComponent,\n        CheckboxLinkExampleComponent,\n        CheckboxBasicExampleComponent,\n        CheckboxExampleComponent,\n        CheckboxVisualTestComponent,\n        CheckboxTestComponent,\n    ],\n    providers: [\n        {\n            provide: DEMO_PATH_TOKEN,\n            useFactory: () => (<any>require).context(`!!raw-loader!./`, true, /.*\\.(ts|html|less)$/),\n        },\n    ],\n    exports: [\n        RouterModule,\n    ],\n})\nexport class CheckboxModule {\n}\n";